.main-navigation__toggle {
  position: absolute;
  top: 12px;
  right: 20px;
  box-sizing: border-box;
  width: 24px;
  height: 18px;
  padding: 0;
  border: none;
  background-color: transparent;
}
.main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  right: 0;
  left: 0;
  height: 2px;
  background-color: #000000;
}
.main-navigation__toggle:hover {
  opacity: 0.6;
}
.main-navigation__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}

.main-navigation__list {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-navigation__list {
    position: static;
  }
}
@media (min-width: 1360px) {
  .main-navigation__list {
    background-color: transparent;
  }
}

.main-navigation--closed .main-navigation__toggle {
  border-top: 2px solid #000000;
  border-bottom: 2px solid #000000;
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__list {
    display: none;
  }
}
.main-navigation--opened .main-navigation__toggle {
  transform: scaleY(1.06);
  transform-origin: center top;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: calc(50% - 1px);
  right: 0;
  left: 0;
  height: 2px;
  background-color: #000000;
  transform: rotate(45deg);
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
.main-navigation--nojs .main-navigation__list {
  position: static;
}

