  height: 302px;
  background-color: #68b738;
  background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/backgrounds/hero-background-cat.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
