  .hero::before {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/backgrounds/hero-background-cat@2x.jpg");
  }
}
@media (min-width: 768px) {
