    display: none;
  }
}
@media (min-width: 1360px) {
  .hero::before {
    left: 50%;
    display: block;
    height: 694px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/backgrounds/hero-background-cat-desktop.jpg");
  }
}
@media (min-width: 1360px) and (min-resolution: 2dppx) {
