}
@media (min-width: 768px) {
  .features__list {
    position: relative;
    grid-template-columns: 1fr 1fr;
    gap: 30px 60px;
  }
  .features__list::after {
    content: "";
    position: absolute;
    right: -30px;
    bottom: 0;
    left: -30px;
    height: 1px;
    background-color: #e7e7e7;
  }
}
@media (min-width: 768px) and (min-width: 1360px) {
