    display: none;
  }
}
@media (min-width: 1360px) {
  .features__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}

.features__item {
  display: grid;
  grid-template-columns: min-content 1fr;
  align-items: center;
  gap: 10px 20px;
  counter-increment: features;
}
.features__item::before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .features__item::before {
    margin-bottom: 33px;
    width: 80px;
    height: 80px;
  }
}
@media (min-width: 768px) {
  .features__item {
    position: relative;
    display: block;
    width: 80%;
    min-height: 287px;
  }
  .features__item::after {
    content: counter(features);
    position: absolute;
    z-index: -1;
    top: -39px;
    right: 14px;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 280px;
    line-height: 280px;
    color: #eeeeee;
  }
}
@media (min-width: 1360px) {
  .features__item {
    width: 100%;
    min-height: 234px;
  }
  .features__item::after {
    right: 0;
  }
}

.features__text {
  grid-column: 2/3;
  margin-top: 0;
